:root {
  --main-radius: 3px;
  --main-color: #007bff;
  --main-hover-color: #0056b3;
  --main-dark-color: #060708;
  --main-dark-12-color: #191b1e;
  --main-overlay: rgba(0, 0, 0, 0.5);
  --main-base-dark-color: #343a40;
  --main-transition-duration: 100ms;
  --main-padding-width: 1rem;
  --main-margin-width: 1rem;
  --main-gray: gray;
  --main-gray-hover: #676767;
  --white: white;
  --white-hover: #cccccc;
}

.modal-open {
  overflow-y: auto !important;
  padding-right: 0 !important;
}

.modal-content {
  background-color: var(--main-dark-12-color);
  color: var(--white);
  border: unset;
}

.modal-footer {
  padding-top: 0;
  border-top: none;
}

.modal-header {
  border-bottom: none;
  padding-bottom: 0;
}
.modal-header .nav-link {
  color: var(--white);
}
.modal-header .nav-link.active {
  color: var(--main-color);
  font-weight: 500;
}
.modal-header .modal-title {
  font-weight: unset;
}

.modal-body {
  padding-top: 0;
  padding-bottom: 0;
}

.popover .popover-header,
.popover .popover-body {
  background-color: unset;
  color: unset;
  padding: unset;
}
.popover .popover-header {
  font-size: 14px;
  text-align: left;
  border: unset;
}
.popover .popover-arrow::after,
.popover .popover-header::before {
  border-bottom-color: var(--main-dark-12-color) !important;
}

.btn-unstyle {
  background-color: unset !important;
  border: none;
  padding: 0;
}
.btn-unstyle:hover, .btn-unstyle:active {
  background-color: unset !important;
  border: none;
}

.search input {
  border-radius: 15px;
  width: 120px;
  margin: auto;
  background-color: var(--main-dark-12-color);
  transition: width var(--main-transition-duration) ease;
  color: var(--white);
  height: 35px;
  border-color: transparent;
  font-size: 1.05rem;
  padding-right: 45px;
}
.search input:active, .search input:focus {
  color: var(--white);
  width: 300px;
  background-color: var(--main-dark-12-color);
  border-color: transparent;
}
.search input::placeholder {
  color: #4b545c;
}

html,
body,
#root {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  min-height: 100vh;
  height: 100%;
  display: flex;
  flex-direction: column;
  background-color: var(--main-dark-color);
  scrollbar-gutter: stable;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

small, .small {
  font-size: 13px;
}

.container-fluid, .container-sm, .container-md, .container-lg, .container-xl, .container-xxl {
  max-width: 95vw;
}

.expand .modal-content {
  background-color: black;
}

.settings-modal .nav-item {
  text-align: center;
}
.settings-modal .nav-advanced {
  min-width: 75px;
}
.settings-modal .nav-default {
  min-width: 40px;
}
.settings-modal .nav-link.disabled {
  color: var(--main-base-dark-color);
}

.search svg circle,
.search svg path {
  stroke: var(--white);
}

.settings svg path {
  fill: var(--white) !important;
}

@keyframes spinBackward {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(-360deg);
  }
}
@keyframes spinForward {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
